@import '../../colours';

.home {
  height: 100vh;

  .content {
    display: inline-flex;
    margin-bottom: 10%;
    margin-left: 10%;
    margin-right: 5%;
    margin-top: 10%;

    .text {
      width: 75%;

      h1 {
        font-size: 70px;
      }

      h2 {
        color: $main-colour;
        font-size: 50px;
        font-weight: lighter;
      }

      p {
        font-size: 30px;
        padding-top: 20px;
      }

      .highlight {
        color: $main-colour;
      }

      .spotify {
        font-size: 16px;
        margin-top: 100px;

        h4 {
          color: $main-colour;
          font-size: 18px;
        }

        ul {
          list-style: none;
          margin: 0;
          padding: 0;
        }
      }
    }

    img {
      bottom: 0;
      margin-right: 2%;
      position: absolute;
      right: 0;
      width: 700px;
      z-index: -1;
    }
  }
}
